const form = require('@/components/common/tui-validation/tui-validation.js');
var { parseTime } = require('wx-tool');

export default {
	data() {
		return {
			form: {
				community: undefined,
				building: undefined,
				unit: undefined,
				contact: undefined,
				mobile: undefined,
				deposit: undefined,
				status: 0,
				note: undefined,
				create_time: undefined,
				create_by: undefined,
				files: []
			},
			statusItems: [
				{
					name: '未开始',
					value: 0,
					checked: true
				},
				{
					name: '进行中',
					value: 1
				},
				{
					name: '完成未清帐',
					value: 2
				},
				{
					name: '已完成',
					value: 3
				}
			],

			type: undefined,
			id: undefined
		};
	},
	onLoad: function(params) {
		this.type = params.type;
		if (this.type === 'detail' || this.type === 'edit') {
			this.id = params.id;
			this.getData(this.id);
		}
	},
	methods: {
		getData: function(id) {
			const db = wx.cloud.database({
				env: 'jnmc-ronxp'
			});
			db.collection('order')
				.doc(id)
				.get()
				.then(response => {
					const data = response.data;
					if (data) {
						this.form = {
							community: data.community,
							building: data.building,
							unit: data.unit,
							contact: data.contact,
							mobile: data.mobile,
							deposit: data.deposit,
							status: data.status,
							note: data.note,
							create_time: data.create_time,
							create_by: data.create_by,
							files: data.files
						};
					}
				});
		},
		statusChange: function(e) {
			this.form.status = Number(e.target.value);
		},
		formSubmit: function(e) {
			//表单规则
			let rules = [
				{
					name: 'community',
					rule: ['required', 'maxLength:50'],
					msg: ['请输入小区名称', '小区名称不能超过50个字']
				},
				{
					name: 'building',
					rule: ['required', 'maxLength:10'],
					msg: ['请输入楼号', '楼号不能超过10个字']
				},
				{
					name: 'unit',
					rule: ['required', 'maxLength:10'],
					msg: ['请输入单元号', '单元号不能超过10个字']
				},
				{
					name: 'contact',
					rule: ['required', 'maxLength:50'],
					msg: ['请输入联系人姓名', '联系人姓名不能超过50个字']
				},
				{
					name: 'mobile',
					rule: ['required', 'maxLength:50'],
					msg: ['请输入联系人电话号码', '联系人电话号码不能超过50个字']
				}
			];
			//进行表单检查
			let formData = e.detail.value;
			let checkRes = form.validation(formData, rules);
			if (!checkRes) {
				let db = wx.cloud.database({
					env: 'jnmc-ronxp'
				});

				if (this.type === 'edit') {
					console.log(this.form);
					db.collection('order')
						.doc(this.id)
						.update({
							data: this.form,
							success: res => {
								uni.showToast({
									title: '修改成功',
									icon: 'success',
									success: function() {
										setTimeout(function() {
											uni.switchTab({
												url: './index',
												success: function(e) {
													const page = getCurrentPages().pop();
													if (!page) return;
													page.onLoad();
												}
											});
										}, 1000);
									}
								});
							},
							fail: err => {
								console.log('修改成功', err);
							}
						});
				} else {
					this.form.create_time = parseTime(Date.parse(new Date()), '{y}-{m}-{d} {h}:{i}:{s}');
					this.form.create_by = '天道酬勤';
					db.collection('order').add({
						data: this.form,
						success: res => {
							uni.showToast({
								title: '添加成功',
								icon: 'success',
								success: function() {
									setTimeout(function() {
										uni.switchTab({
											url: './index',
											success: function(e) {
												const page = getCurrentPages().pop();
												if (!page) return;
												page.onLoad();
											}
										});
									}, 1000);
								}
							});
						},
						fail: err => {
							console.log('添加成功', err);
						}
					});
				}
			} else {
				uni.showToast({
					title: checkRes,
					icon: 'none'
				});
			}
		},
		chooseImage: function(e) {
			let that = this;
			if (that.form.files.length >= 9) {
				this.tui.toast('最多上传9张图片');
				return;
			}
			wx.chooseImage({
				count: 9 - that.form.files.length,
				sizeType: ['original', 'compressed'],
				sourceType: ['album', 'camera'],
				success: function(res) {
					if (res.tempFilePaths) {
						let fileIDS = [];
						res.tempFilePaths.forEach(tempFilePath => {
							wx.cloud.uploadFile({
								cloudPath: 'order/' + new Date().getTime() + '-' + Math.floor(Math.random() * 1000),
								filePath: tempFilePath,
								success: response => {
									fileIDS.push(response.fileID);
									if (fileIDS.length == res.tempFilePaths.length) {
										wx.cloud.getTempFileURL({
											fileList: fileIDS,
											success: result => {
												if (result && result.fileList) {
													result.fileList.forEach(item => {
														that.form.files.push({ id: item.fileID, url: item.tempFileURL });
													});
												}
											},
											fail: err => {
												console.log(err);
											}
										});
									}
								},
								fail: err => {
									console.log(err);
								}
							});
						});
					}
				}
			});
		},
		previewImage: function(e) {
			uni.previewImage({
				current: e.currentTarget.id,
				urls: this.form.files.map(item => item.url)
			});
		},
		deleteImage: function(fileID) {
			wx.cloud.deleteFile({
				fileList: [fileID],
				success: res => {
					console.log(res);
					this.form.files.splice(this.form.files.findIndex(item => item.id === fileID), 1);
				},
				fail: err => {
					console.log(err);
				}
			});
		},
		call: function() {
			uni.makePhoneCall({
				phoneNumber: this.form.mobile,
				success: res => {
					console.log('调用成功!');
				},
				fail: res => {
					console.log('调用失败!');
				}
			});
		}
	}
};
